import React, { createContext, useContext, useState, useEffect } from 'react';
import { authService, User, LoginCredentials, RegisterData } from '../services/auth';
import { IS_OFFLINE } from '../config';

interface AuthContextType {
  user: User | null;
  loading: boolean;
  login: (credentials: LoginCredentials) => Promise<void>;
  register: (data: RegisterData) => Promise<void>;
  logout: () => void;
  isAuthenticated: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    const initAuth = async () => {
      try {
        if (IS_OFFLINE) {
          // In offline mode, always use local user
          const localUser = { id: '1', email: 'local@local', name: 'Local User' };
          setUser(localUser);
          setIsAuthenticated(true);
          authService.setToken('local-token');
          authService.setUser(localUser);
        } else {
          const currentUser = await authService.getCurrentUser();
          setUser(currentUser);
          setIsAuthenticated(!!currentUser);
        }
      } catch (error) {
        console.error('Error initializing auth:', error);
      } finally {
        setLoading(false);
      }
    };

    initAuth();
  }, []);

  const login = async (credentials: LoginCredentials) => {
    if (IS_OFFLINE) {
      // In offline mode, always use local user
      const localUser = { id: '1', email: 'local@local', name: 'Local User' };
      setUser(localUser);
      setIsAuthenticated(true);
      authService.setToken('local-token');
      authService.setUser(localUser);
    } else {
      const { user } = await authService.login(credentials);
      setUser(user);
      setIsAuthenticated(true);
    }
  };

  const register = async (data: RegisterData) => {
    if (IS_OFFLINE) {
      throw new Error('Registration is not supported in offline mode');
    }
    const { user } = await authService.register(data);
    setUser(user);
    setIsAuthenticated(true);
  };

  const logout = () => {
    if (!IS_OFFLINE) {  // Don't allow logout in offline mode
      authService.logout();
      setUser(null);
      setIsAuthenticated(false);
    }
  };

  const value = {
    user,
    loading,
    login,
    register,
    logout,
    isAuthenticated,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};
