import React, { useCallback, useEffect, useRef, useState } from 'react';
import 'katex/dist/katex.min.css';
import { Note } from '../../types';
import { CodeBlockWrapper } from '../CodeBlock/CodeBlockWrapper';
import { Trash2 } from 'react-feather';
import { SearchModal } from '../Search/SearchModal';
import { Backlinks } from '../Backlinks';
import { TagEditor } from '../TagEditor/TagEditor';
import { AIAssistant } from '../AIAssistant/AIAssistant';
import debounce from 'lodash.debounce';
import { useKeyboardShortcuts } from '../../hooks/useKeyboardShortcuts';
import { on } from 'events';
import useDebouncedEffect from '../../hooks/useDebouncedEffects';

interface NotebookProps {
  note: Note;
  notes: Note[];
  backlinks: Note[];
  onUpdate: (note: Note) => void;
  onDelete?: () => void;
  onNoteClick: (noteId: string) => void;
}

const Notebook: React.FC<NotebookProps> = React.memo(({ note, notes, backlinks, onUpdate, onDelete, onNoteClick }) => {
  const [localNote, setLocalNote] = useState(note);
  const [lastSavedContent, setLastSavedContent] = useState(note.content || '');
  const [isDirty, setIsDirty] = useState(false);
  const [isSearchOpen, setIsSearchOpen] = useState(false);
  const [isTocCollapsed, setIsTocCollapsed] = useState(true);
  const [tocWidth, setTocWidth] = useState(264);

  // Refs to store the latest state values
  const lastSavedContentRef = useRef(lastSavedContent);
  const noteRef = useRef(note);
  const localNoteRef = useRef(localNote);

  // Update refs when state changes
  useEffect(() => {
    lastSavedContentRef.current = lastSavedContent;
  }, [lastSavedContent]);

  useEffect(() => {
    noteRef.current = note;
  }, [note]);

  useEffect(() => {
    localNoteRef.current = localNote;
  }, [localNote]);

  // Debounced save function using lodash.debounce
  const debouncedSave = useRef(
    debounce(() => {
      const currentContent = localNote.content;
      if (currentContent !== lastSavedContentRef.current) {
        setIsDirty(true);
        const updatedNote = {
          ...noteRef.current,
          content: currentContent,
          last_modified: new Date().toISOString(),
          code_outputs: noteRef.current.code_outputs || {},
          tags: localNoteRef.current.tags || [],
          references: noteRef.current.references || [],
          backlinks: noteRef.current.backlinks || []
        };
        onUpdate(updatedNote);
        setLastSavedContent(currentContent);
        setIsDirty(false);
      }
    }, 1000) // 5000ms = 5 seconds
  ).current;

  // Sync with external note changes
  useEffect(() => {
    setLocalNote(note);
    setLastSavedContent(note.content || '');
    setIsDirty(false);
    debouncedSave.cancel(); // Cancel any pending saves
  }, [note, debouncedSave]);

  // Debounced save effect
  useEffect(() => {
    debouncedSave();
    
    // Cleanup on unmount or when dependencies change
    return () => {
      debouncedSave.cancel();
    };
  }, [localNote, debouncedSave]);

  // Save changes when unmounting
  useEffect(() => {
    return () => {
      const currentContent = localNote.content;
      if (currentContent !== lastSavedContentRef.current) {
        const updatedNote = {
          ...noteRef.current,
          content: currentContent,
          last_modified: new Date().toISOString(),
          code_outputs: noteRef.current.code_outputs || {},
          tags: localNoteRef.current.tags || [],
          references: noteRef.current.references || [],
          backlinks: noteRef.current.backlinks || []
        };
        // onUpdate(updatedNote);
      }
      debouncedSave.cancel(); // Ensure no pending saves
    };
  }, [localNote, note]);

  const checkIfInCodeBlock = () => {
    const textarea = document.activeElement;
    if (!textarea || !(textarea instanceof HTMLTextAreaElement)) {
      return false;
    }

    const { selectionStart, value } = textarea;
    if (typeof selectionStart !== 'number' || !value) {
      return false;
    }

    const lines = value.split('\n');
    const currentLineIndex = value.slice(0, selectionStart).split('\n').length - 1;
    
    // Track if we're in a code block by counting ``` markers
    let inCodeBlock = false;
    for (let i = 0; i <= currentLineIndex; i++) {
      if (lines[i]?.trim().startsWith('```')) {
        inCodeBlock = !inCodeBlock;
      }
    }
    
    return inCodeBlock;
  };

  // Handle content change
  const handleContentChange = useCallback((e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newContent = e.target.value;
    setLocalNote(prev => ({
      ...prev,
      content: newContent,
      last_modified: new Date().toISOString()
    }));
  }, []);

  // Handle paragraph key down events
  const handleParagraphKeyDown = useCallback((e: React.KeyboardEvent, index: number) => {
    const isInCodeBlock = checkIfInCodeBlock();
    if (isInCodeBlock) {
      return; // Let default behavior handle code blocks
    }

    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      const textarea = e.target as HTMLTextAreaElement;
      if (!textarea) return;

      const { selectionStart } = textarea;
      const currentContent = localNote.content;
      
      // Handle code block splitting
      const beforeCursor = currentContent.slice(0, selectionStart);
      const afterCursor = currentContent.slice(selectionStart);
      
      // Check if we're splitting a code block
      const beforeLines = beforeCursor.split('\n');
      const afterLines = afterCursor.split('\n');
      let inCodeBlock = false;
      let codeBlockStart = false;
      let codeBlockEnd = false;

      // Check for code block markers in the content before cursor
      for (const line of beforeLines) {
        if (line.trim().startsWith('```')) {
          inCodeBlock = !inCodeBlock;
          if (inCodeBlock) codeBlockStart = true;
        }
      }

      // Check for code block markers in the content after cursor
      for (const line of afterLines) {
        if (line.trim().startsWith('```')) {
          if (inCodeBlock) codeBlockEnd = true;
          inCodeBlock = !inCodeBlock;
        }
      }

      // If we're splitting a code block, ensure both parts have proper markers
      if (codeBlockStart && !codeBlockEnd) {
        // Add closing marker to first part and opening marker to second part
        const newContent = beforeCursor + '\n```' + '\n' + '```\n' + afterCursor;
        setLocalNote(prev => ({
          ...prev,
          content: newContent,
          last_modified: new Date().toISOString()
        }));
      } else {
        // Normal splitting behavior
        const newContent = beforeCursor + '\n' + afterCursor;
        setLocalNote(prev => ({
          ...prev,
          content: newContent,
          last_modified: new Date().toISOString()
        }));
      }

      // Update note content
      const updatedNote = {
        ...noteRef.current,
        content: localNote.content,
        last_modified: new Date().toISOString(),
        code_outputs: noteRef.current.code_outputs || {},
        tags: localNoteRef.current.tags || [],
        references: noteRef.current.references || [],
        backlinks: noteRef.current.backlinks || []
      };
      onUpdate(updatedNote);
    }
  }, [localNote, note, onUpdate]);

  // Handle save
  const handleSave = useCallback(() => {
    const currentContent = localNote.content;
    const updatedNote = {
      ...note,
      content: currentContent,
      last_modified: new Date().toISOString()
    };
    setLocalNote(prev => ({
      ...prev,
      content: currentContent,
      last_modified: new Date().toISOString()
    }));
    onUpdate(updatedNote);
  }, [localNote, note, onUpdate]);

  // Handle key down events for shortcuts
  const handleKeyDown = useCallback((e: React.KeyboardEvent) => {
    const isInCodeBlock = checkIfInCodeBlock();
    if (isInCodeBlock) {
      // Allow new line in code block without preventing default behavior
      return;
    }

    // Handle Enter key
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      const currentContent = localNote.content;
      const updatedNote = {
        ...noteRef.current,
        content: currentContent,
        last_modified: new Date().toISOString(),
        code_outputs: noteRef.current.code_outputs || {},
        tags: localNoteRef.current.tags || [],
        references: noteRef.current.references || [],
        backlinks: noteRef.current.backlinks || []
      };
      onUpdate(updatedNote);
    }

    // Handle Ctrl/Cmd + S
    if ((e.ctrlKey || e.metaKey) && e.key === 's') {
      e.preventDefault();
      handleSave();
    }
  }, [localNote, note, onUpdate, handleSave]);

  return (
    <>
      <div className="flex flex-col h-screen">
        <main className={`flex-1 overflow-y-auto ${isDirty ? 'unsaved-changes' : ''}`}>
          <div className="max-w-4xl mx-auto px-8 py-6 min-h-screen bg-gray-50 dark:bg-gray-900">
            {/* Note header with tags and actions */}
            <div className="mb-6">
              <div className="flex items-center justify-between mb-4">
                <TagEditor
                  tags={localNote.tags || []}
                  onAdd={(newTag) => {
                    const normalizedTag = newTag.trim().replace(/^#|[\[\]]/g, '');
                    
                    if (normalizedTag && !(localNote.tags || []).includes(normalizedTag)) {
                      const updatedNote = {
                        ...localNote,
                        tags: [...(localNote.tags || []), normalizedTag],
                        last_modified: new Date().toISOString()
                      };
                      setLocalNote(updatedNote);
                      onUpdate(updatedNote);
                    }
                  }}
                  onRemove={(tagToRemove) => {
                    const updatedNote = {
                      ...localNote,
                      tags: (localNote.tags || []).filter(tag => tag !== tagToRemove),
                      last_modified: new Date().toISOString()
                    };
                    setLocalNote(updatedNote);
                    onUpdate(updatedNote);
                  }}
                />
                {onDelete && (
                  <button
                    onClick={onDelete}
                    className="ml-4 p-2 text-gray-500 hover:text-red-500 dark:text-gray-400 dark:hover:text-red-400 transition-colors rounded-md hover:bg-gray-100 dark:hover:bg-gray-800"
                    title="Delete note"
                  >
                    <Trash2 size={16} />
                  </button>
                )}
              </div>
            </div>

            {/* Note content */}
            <div className="space-y-4">
              <textarea
                value={localNote.content}
                onChange={handleContentChange}
                onKeyDown={(e) => handleKeyDown(e)}
                className="w-full h-full min-h-[500px] p-4 bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 dark:focus:ring-blue-400"
                placeholder="Start writing..."
              />
            </div>
          </div>

          {backlinks.length > 0 && (
            <div className="border-t border-gray-200 dark:border-gray-700">
              <Backlinks notebook={{ notes }} currentNoteId={note.id} onSelect={onNoteClick} />
            </div>
          )}
        </main>
      </div>

      {/* Search modal */}
      <SearchModal
        show={isSearchOpen}
        onClose={() => setIsSearchOpen(false)}
        notes={notes}
        onNoteSelect={onNoteClick}
      />

      {/* AI Assistant */}
      <AIAssistant
        currentNote={note}
        allNotes={notes}
        onInsertContent={(content) => {
          const newContent = note.content + '\n\n' + content;
          setLocalNote(prev => ({
            ...prev,
            content: newContent,
            last_modified: new Date().toISOString()
          }));
          onUpdate({ ...note, content: newContent });
        }}
        onAddTags={(tags) => {
          const newTags = Array.from(new Set([...(note.tags || []), ...tags]));
          setLocalNote(prev => ({
            ...prev,
            tags: newTags,
            last_modified: new Date().toISOString()
          }));
          onUpdate({ ...note, tags: newTags });
        }}
      />

      {/* Optional: Saving indicator */}
      {isDirty && (
        <div className="fixed bottom-4 right-4 bg-blue-500 text-white px-4 py-2 rounded shadow">
          Saving...
        </div>
      )}
    </>
  );
});

export default Notebook;
