import axiosInstance from './axiosConfig';
import { API_URL, IS_OFFLINE, FEATURES } from '../config';

export interface User {
  id: string;
  email: string;
  username?: string;
  name?: string;
}

export interface LoginCredentials {
  email: string;
  password: string;
}

export interface RegisterData extends LoginCredentials {
  name?: string;
  username?: string;
}

export class AuthService {
  private token: string | null = null;
  private user: User | null = null;

  constructor() {
    // In offline mode, always initialize with local user
    if (IS_OFFLINE) {
      this.setLocalUser();
    } else {
      // In online mode, try to restore from localStorage if enabled
      if (FEATURES.LOCAL_STORAGE) {
        this.token = localStorage.getItem('token');
        const savedUser = localStorage.getItem('user');
        if (savedUser) {
          try {
            this.user = JSON.parse(savedUser);
          } catch (e) {
            console.error('Error parsing saved user:', e);
            this.logout();
          }
        }
      }
    }
  }

  async login(credentials: LoginCredentials): Promise<{ user: User; token: string }> {
    if (!IS_OFFLINE) {
      const response = await axiosInstance.post(`${API_URL}/auth/login`, credentials);
      this.setToken(response.data.token);
      this.setUser(response.data.user);
      return response.data;
    } else {
      // In offline mode, always use local user regardless of credentials
      return {
        user: { id: '1', email: 'local@local', name: 'Local User' },
        token: 'local-token'
      };
    }
  }

  async register(data: RegisterData): Promise<{ user: User; token: string }> {
    if (!IS_OFFLINE) {
      const response = await axiosInstance.post(`${API_URL}/auth/register`, data);
      this.setToken(response.data.token);
      this.setUser(response.data.user);
      return response.data;
    } else {
      throw new Error('Registration is not supported in offline mode');
    }
  }

  async getCurrentUser(): Promise<User | null> {
    try {
      if (IS_OFFLINE) {
        // In offline mode, always return local user
        return { id: '1', email: 'local@local', name: 'Local User' };
      }

      const token = this.getToken();
      if (!token) return null;

      const response = await axiosInstance.get(`${API_URL}/auth/me`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      this.setUser(response.data);
      return response.data;
    } catch (error) {
      if (!IS_OFFLINE) {
        this.logout();
      }
      return null;
    }
  }

  setToken(token: string) {
    this.token = token;
    if (IS_OFFLINE || FEATURES.LOCAL_STORAGE) {
      localStorage.setItem('token', token);
    }
  }

  setUser(user: User) {
    this.user = user;
    if (IS_OFFLINE || FEATURES.LOCAL_STORAGE) {
      localStorage.setItem('user', JSON.stringify(user));
    }
  }

  getToken(): string | null {
    if (IS_OFFLINE) {
      return 'local-token';
    }
    return this.token;
  }

  logout() {
    if (!IS_OFFLINE) {  // Don't allow logout in offline mode
      this.token = null;
      this.user = null;
      if (FEATURES.LOCAL_STORAGE) {
        localStorage.removeItem('token');
        localStorage.removeItem('user');
      }
    }
  }

  getUser(): User | null {
    return this.user;
  }

  isAuthenticated(): boolean {
    return !!this.getToken();
  }

  async init() {
    if (process.env.NODE_ENV === 'development' && !IS_OFFLINE) {
      const adminCredentials: LoginCredentials = {
        email: 'admin@wazo.space', // replace with actual admin email
        password: '123456' // replace with actual admin password
      };
      await this.login(adminCredentials);
    }
  }

  private setLocalUser() {
    const localUser = { id: '1', email: 'local@local', name: 'Local User' };
    const localToken = 'local-token';
    this.setToken(localToken);
    this.setUser(localUser);
    localStorage.setItem('user', JSON.stringify(localUser));
  }
}

// Create and export a singleton instance
export const authService = new AuthService();

