import React, { useState, useEffect } from 'react';
import { Task, TaskStatus, TaskPriority } from '../../types/task';
import { taskApi } from '../../api/taskApi';
import TaskList from './TaskList';
import { TaskCalendar } from './TaskCalendar';
import { ListBulletIcon, CalendarIcon } from '@heroicons/react/24/outline';
import useDebouncedEffect from '../../hooks/useDebouncedEffects';

type ViewType = 'list' | 'calendar';

interface TaskManagerProps {
  onTaskSelect?: (task: Task) => void;
}

export const TaskManager: React.FC<TaskManagerProps> = ({ onTaskSelect }) => {
  const [tasks, setTasks] = useState<Task[]>([]);
  const [view, setView] = useState<ViewType>('list');
  const [filter, setFilter] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [pendingUpdates, setPendingUpdates] = useState<Map<string, Partial<Task>>>(new Map());

  const fetchTasks = async () => {
    try {
      setLoading(true);
      const fetchedTasks = await taskApi.getAllTasks();
      setTasks(fetchedTasks);
    } catch (error) {
      console.error('Error fetching tasks:', error);
      setError('Failed to fetch tasks. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchTasks();
  }, []);

  const handleCreateTask = async (taskData: Task) => {
    try {
      setLoading(true);
      await taskApi.createTask(taskData);
      await fetchTasks(); // Refresh the task list
    } catch (error) {
      console.error('Error creating task:', error);
      setError('Failed to create task');
    } finally {
      setLoading(false);
    }
  };

  // Debounced effect for batched updates
  useDebouncedEffect(
    () => {
      if (pendingUpdates.size > 0) {
        const updates = Array.from(pendingUpdates.entries());
        Promise.all(
          updates.map(([taskId, updates]) =>
            taskApi.updateTask(taskId, updates)
          )
        ).then(() => {
          setPendingUpdates(new Map());
          fetchTasks();
        }).catch(error => {
          console.error('Error updating tasks:', error);
          setError('Failed to update tasks');
        });
      }
    },
    1000, // 1 second delay
    [pendingUpdates]
  );

  const handleUpdateTask = async (taskId: string, updates: Partial<Task>) => {
    setPendingUpdates(prev => {
      const newUpdates = new Map(prev);
      const existingUpdates = newUpdates.get(taskId) || {};
      newUpdates.set(taskId, { ...existingUpdates, ...updates });
      return newUpdates;
    });
  };

  const handleDeleteTask = async (taskId: string) => {
    try {
      await taskApi.deleteTask(taskId);
      fetchTasks();
    } catch (error) {
      console.error('Error deleting task:', error);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-full">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center justify-center h-full">
        <div className="text-red-500">{error}</div>
      </div>
    );
  }

  return (
    <div className="h-full flex flex-col">
      <div className="flex justify-between items-center px-4 py-3 bg-white border-b">
        <div className="flex items-center space-x-2">
          <button
            onClick={() => setView('list')}
            className={`flex items-center gap-1.5 px-2.5 py-1.5 rounded-md text-sm font-medium transition-colors ${
              view === 'list' 
                ? 'bg-blue-50 text-blue-600' 
                : 'text-gray-700 hover:bg-gray-100'
            }`}
          >
            <ListBulletIcon className="h-4 w-4" />
            List
          </button>
          <button
            onClick={() => setView('calendar')}
            className={`flex items-center gap-1.5 px-2.5 py-1.5 rounded-md text-sm font-medium transition-colors ${
              view === 'calendar' 
                ? 'bg-blue-50 text-blue-600' 
                : 'text-gray-700 hover:bg-gray-100'
            }`}
          >
            <CalendarIcon className="h-4 w-4" />
            Calendar
          </button>
        </div>
      </div>

      <div className="flex-1 min-h-0">
        {view === 'list' ? (
          <TaskList
            tasks={tasks}
            onEditTask={(task) => handleUpdateTask(task.id, task)}
            onDeleteTask={handleDeleteTask}
            onCreateTask={handleCreateTask}
            onUpdateTask={handleUpdateTask}
            filter={filter}
            onFilterChange={setFilter}
          />
        ) : (
          <TaskCalendar
            tasks={tasks}
            onTaskDelete={handleDeleteTask}
            onTaskSelect={onTaskSelect}
          />
        )}
      </div>
    </div>
  );
};
