import React, { useState, useEffect, useCallback } from 'react';
import Notebook from './components/Notebook/Notebook';
import { Note } from './types';
import { useKeyboardShortcuts } from './hooks/useKeyboardShortcuts';
import { NoteGraph } from './components/Graph';
import { SearchModal } from './components/Search/SearchModal';
import { Header } from './components/Header/Header'; // Import the correct Header component
import { Settings } from './components/Settings/Settings';
import { ThemeProvider } from './context/ThemeContext';
import { TaskView } from './components/Tasks/TaskView';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import Login from './pages/auth/Login';
import Register from './pages/auth/Register';
import axiosInstance from './services/axiosConfig'; // Import axios
import { isAxiosError } from 'axios';
import { SplashScreen } from './components/SplashScreen/SplashScreen';
import LandingPage from './pages/LandingPage';
import SignInPage from './pages/auth/Login'; // Import SignInPage
import { IS_OFFLINE } from './config';

interface ProtectedRouteProps {
  children: React.ReactNode;
}

const ProtectedRoute: React.FC<ProtectedRouteProps> = ({ children }) => {
  const { isAuthenticated, loading } = useAuth();

  if (loading) {
    return <div>Loading...</div>;
  }

  // In offline mode, always allow access
  if (IS_OFFLINE) {
    return <>{children}</>;
  }

  // In online mode, redirect to login if not authenticated
  if (!isAuthenticated) {
    return <Navigate to="/login" />;
  }

  return <>{children}</>;
};

function AppContent() {
  const [notes, setNotes] = useState<Note[]>([]);
  const [selectedNoteId, setSelectedNoteId] = useState<string | null>(() => {
    const saved = localStorage.getItem('selectedNoteId');
    return saved || null;
  });
  const [showSearch, setShowSearch] = useState(false);
  const [showGraph, setShowGraph] = useState<boolean>(() => {
    const savedGraphState = localStorage.getItem('showGraph');
    return savedGraphState === 'true';
  });
  const [showSettings, setShowSettings] = useState(false);
  const [showTasks, setShowTasks] = useState(false);
  const [showSplashScreen, setShowSplashScreen] = useState(false);

  const { isAuthenticated } = useAuth();

  useEffect(() => {
    if (isAuthenticated) {
      setShowSplashScreen(!localStorage.getItem('hasSeenSplashScreen'));
    }
  }, [isAuthenticated]);

  const fetchNotes = useCallback(async () => {
    if (IS_OFFLINE) {
      // In offline mode, use local storage for notes
      const savedNotes = localStorage.getItem('notes');
      const notesList = savedNotes ? JSON.parse(savedNotes) : [];
      
      // If no notes exist, create a default note
      if (notesList.length === 0) {
        const defaultNote = {
          id: '1',
          content: '# Welcome to Offline Mode\n\nThis is your first note.',
          created_at: new Date().toISOString(),
          last_modified: new Date().toISOString(),
          tags: [],
          references: [],
          backlinks: [],
          suggested_links: []
        };
        notesList.push(defaultNote);
        localStorage.setItem('notes', JSON.stringify(notesList));
      }
      
      setNotes(notesList);
      
      // If no note is selected, select the first one
      if (!selectedNoteId && notesList.length > 0) {
        setSelectedNoteId(notesList[0].id);
      }
      return;
    }

    try {
      const response = await axiosInstance.get('/api/notes');
      const data = response.data;
      const notesList = Array.isArray(data) ? data : data.notes || [];
      setNotes(notesList);
      
      // If no note is selected, select the first one
      if (!selectedNoteId && notesList.length > 0) {
        setSelectedNoteId(notesList[0].id);
      } else if (selectedNoteId) {
        const noteExists = notesList.some((note: Note) => note.id === selectedNoteId);
        if (!noteExists && notesList.length > 0) {
          setSelectedNoteId(notesList[0].id);
        } else if (!noteExists) {
          setSelectedNoteId(null);
        }
      }
    } catch (error) {
      console.error('Error fetching notes:', error);
    }
  }, [selectedNoteId]);

  useEffect(() => {
    if (isAuthenticated && !IS_OFFLINE) {
      // Only fetch notes in online mode when authenticated
      fetchNotes();
    } else if (IS_OFFLINE) {
      // In offline mode, just load from localStorage
      fetchNotes();
    } else {
      setNotes([]);
    }
  }, [isAuthenticated, fetchNotes]);

  // Save notes to localStorage in offline mode
  useEffect(() => {
    if (IS_OFFLINE) {
      localStorage.setItem('notes', JSON.stringify(notes));
    }
  }, [notes]);

  const handleCreateNote = useCallback(async () => {
    try {
      if (IS_OFFLINE) {
        // In offline mode, create note locally
        const newNote: Note = {
          id: Date.now().toString(), // Use timestamp as ID
          title: 'New Note', // Add a default title
          content: '',
          created_at: new Date().toISOString(),
          last_modified: new Date().toISOString(),
          tags: [],
          references: [],
          backlinks: [],
          code_outputs: {}, // Initialize as empty object
          suggested_links: [] // Assuming this is also needed
        };
        
        setNotes(prevNotes => [...prevNotes, newNote]);
        setSelectedNoteId(newNote.id);
        setShowSearch(false);
        
        // Update localStorage
        const savedNotes = localStorage.getItem('notes');
        const notesList = savedNotes ? JSON.parse(savedNotes) : [];
        notesList.push(newNote);
        localStorage.setItem('notes', JSON.stringify(notesList));
        return;
      }

      const newNote = {
        content: '',
        created_at: new Date().toISOString(),
        last_modified: new Date().toISOString(),
        tags: [],
      };

      const response = await axiosInstance.post(`/api/notes`, newNote);
      const createdNote = response.data;
      setNotes(prevNotes => [...prevNotes, createdNote]);
      setSelectedNoteId(createdNote.id);
      setShowSearch(false);
    } catch (error) {
      console.error('Error creating note:', error);
    }
  }, []);

  const handleDeleteNote = useCallback(async (noteId: string) => {
    try {
      if (IS_OFFLINE) {
        // In offline mode, delete note locally
        setNotes(prevNotes => {
          const updatedNotes = prevNotes.filter(note => note.id !== noteId);
          localStorage.setItem('notes', JSON.stringify(updatedNotes));
          return updatedNotes;
        });

        if (selectedNoteId === noteId) {
          const remainingNotes = notes.filter(note => note.id !== noteId);
          const newSelectedId = remainingNotes.length > 0 ? remainingNotes[0].id : null;
          setSelectedNoteId(newSelectedId);
        }
        return;
      }

      await axiosInstance.delete(`/api/notes/${noteId}`);
      setNotes(prevNotes => {
        const updatedNotes = prevNotes.filter(note => note.id !== noteId);
        return updatedNotes;
      });

      if (selectedNoteId === noteId) {
        const remainingNotes = notes.filter(note => note.id !== noteId);
        const newSelectedId = remainingNotes.length > 0 ? remainingNotes[0].id : null;
        setSelectedNoteId(newSelectedId);
      }
    } catch (error) {
      console.error('Error deleting note:', error);
    }
  }, [notes, selectedNoteId]);

  const handleUpdateNote = useCallback(async (updatedNote: Note) => {
    try {
      if (IS_OFFLINE) {
        // In offline mode, update note locally
        setNotes(prevNotes => {
          const updatedNotes = prevNotes.map(note =>
            note.id === updatedNote.id ? updatedNote : note
          );
          localStorage.setItem('notes', JSON.stringify(updatedNotes));
          return updatedNotes;
        });
        return;
      }

      const noteToUpdate = {
        content: updatedNote.content,
        last_modified: new Date().toISOString(),
        tags: updatedNote.tags || [],
        code_outputs: updatedNote.code_outputs || {},
        backlinks: updatedNote.backlinks || [],
        references: updatedNote.references || [],
        suggested_links: updatedNote.suggested_links || []
      };

      const response = await axiosInstance.put(`/api/notes/${updatedNote.id}`, noteToUpdate);
      const updatedNoteFromServer = response.data;

      setNotes(prevNotes =>
        prevNotes.map(note =>
          note.id === updatedNoteFromServer.id ? updatedNoteFromServer : note
        )
      );
    } catch (error) {
      console.error('Error updating note:', error);
    }
  }, []);

  const handleSelectNote = useCallback((noteId: string) => {
    if (selectedNoteId === noteId) return; // Don't reselect the same note
    
    setSelectedNoteId(noteId);
    // Only fetch if the note isn't in our current state
    const selectedNote = notes.find(note => note.id === noteId);
    if (selectedNote) {
      // Skip immediate update unless necessary
      const lastUpdate = new Date(selectedNote.last_modified);
      const fiveMinutesAgo = new Date(Date.now() - 5 * 60 * 1000);
      
      if (lastUpdate < fiveMinutesAgo) {
        handleUpdateNote({
          ...selectedNote,
          last_modified: new Date().toISOString()
        });
      }
    }
  }, [notes, selectedNoteId, handleUpdateNote]);

  const handlePanelToggle = (panel: 'search' | 'settings' | 'tasks') => {
    // Close all other panels first
    if (panel !== 'search') setShowSearch(false);
    if (panel !== 'settings') setShowSettings(false);
    if (panel !== 'tasks') setShowTasks(false);

    // Toggle the requested panel
    switch (panel) {
      case 'search':
        setShowSearch(prev => !prev);
        break;
      case 'settings':
        setShowSettings(prev => !prev);
        break;
      case 'tasks':
        setShowTasks(prev => !prev);
        break;
    }
  };

  // Register keyboard shortcuts
  useKeyboardShortcuts({
    onNewNote: handleCreateNote,
    onToggleGraph: () => setShowGraph(!showGraph),
    onSearch: () => handlePanelToggle('search'),
    onToggleTasks: () => handlePanelToggle('tasks')
  });

  const selectedNote = notes.find(note => note.id === selectedNoteId);

  const handleCloseSplashScreen = useCallback(() => {
    setShowSplashScreen(false);
    localStorage.setItem('hasSeenSplashScreen', 'true');
  }, []);

  const handleUpdateNoteSelection = useCallback(async (updatedNote: Note) => {
    await handleUpdateNote(updatedNote);
  }, [handleUpdateNote]);

  return (
    <div className="h-screen flex flex-col bg-white dark:bg-gray-900">
      {showSplashScreen && <SplashScreen onClose={handleCloseSplashScreen} />}
      <div className="h-screen flex flex-col">
        <Header
          onNewNote={handleCreateNote}
          onToggleGraph={() => setShowGraph(!showGraph)}
          onToggleSearch={() => handlePanelToggle('search')}
          onToggleSettings={() => handlePanelToggle('settings')}
          onToggleTasks={() => handlePanelToggle('tasks')}
        />
        
        <div className="flex-1 flex relative overflow-hidden bg-gray-50">
          {/* Main Content */}
          <div className={`w-full transition-transform duration-300 ease-in-out ${
            showTasks || showSettings || showSearch ? 'translate-x-[-20%]' : 'translate-x-0'
          }`}>
            {showGraph ? (
              <div className="h-[calc(100vh-3.5rem)]">
                {selectedNote && (
                  <NoteGraph
                    currentNote={selectedNote}
                    allNotes={notes}
                    onNodeClick={handleSelectNote}
                    onUpdateNote={handleUpdateNoteSelection}
                  />
                )}
              </div>
            ) : (
              selectedNote && (
                <Notebook
                  note={selectedNote}
                  notes={notes}
                  backlinks={notes.filter(n => n.references?.includes(selectedNote.id))}
                  onUpdate={handleUpdateNoteSelection}
                  onDelete={() => handleDeleteNote(selectedNote.id)}
                  onNoteClick={handleSelectNote}
                />
              )
            )}
          </div>

          {/* Side Panels Container */}
          <div className={`fixed top-[3.5rem] bottom-0 right-0 w-[40%] bg-gray-50 transition-transform duration-300 ease-in-out ${
            showTasks || showSettings || showSearch ? 'translate-x-0' : 'translate-x-full'
          }`}>
            {/* Task Panel */}
            <div className={`absolute inset-0 transition-transform duration-300 ease-in-out bg-gray-50 ${
              showTasks ? 'translate-x-0' : 'translate-x-full'
            }`}>
              <TaskView show={showTasks} />
            </div>

            {/* Settings Panel */}
            <div className={`absolute inset-0 transition-transform duration-300 ease-in-out bg-gray-50 ${
              showSettings ? 'translate-x-0' : 'translate-x-full'
            }`}>
              <Settings show={showSettings} />
            </div>

            {/* Search Panel */}
            <div className={`absolute inset-0 transition-transform duration-300 ease-in-out bg-gray-50 ${
              showSearch ? 'translate-x-0' : 'translate-x-full'
            }`}>
              <SearchModal
                show={showSearch}
                onClose={() => handlePanelToggle('search')}
                notes={notes}
                onNoteSelect={(noteId) => {
                  setSelectedNoteId(noteId);
                  handlePanelToggle('search');
                }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

function AppRouter() {
  const { isAuthenticated, loading } = useAuth();

  if (loading) {
    return <div>Loading...</div>;
  }

  // In offline mode, always render the app
  if (IS_OFFLINE) {
    return (
      <Routes>
        <Route path="/*" element={<AppContent />} />
      </Routes>
    );
  }

  return (
    <Routes>
      <Route path="/" element={<LandingPage />} />
      <Route path="/login" element={<Login />} />
      <Route path="/register" element={<Register />} />
      <Route
        path="/app/*"
        element={
          <ProtectedRoute>
            <AppContent />
          </ProtectedRoute>
        }
      />
      <Route path="*" element={<Navigate to="/" />} />
    </Routes>
  );
}

function App() {
  return (
    <Router>
      <ThemeProvider>
        <AuthProvider>
          <AppRouter />
        </AuthProvider>
      </ThemeProvider>
    </Router>
  );
}

export default App;
