import React, { useState, useEffect } from 'react';
import { BasePanel } from '../common/BasePanel';
import { Switch, FormControlLabel } from '@mui/material';
import useDebouncedEffect from '../../hooks/useDebouncedEffects';
import axiosInstance from '../../services/axiosConfig';

interface SettingsProps {
  show: boolean;
}

interface Settings {
  notesDirectory: string;
  ollamaModel: string;
  ollamaEndpoint: string;
  theme: 'light' | 'dark';
}

const defaultSettings: Settings = {
  notesDirectory: './data/notes',
  ollamaModel: 'mistral',
  ollamaEndpoint: process.env.REACT_APP_OLLAMA_ENDPOINT || 'http://localhost:11434/api',
  theme: 'light'
};


export const Settings: React.FC<SettingsProps> = ({ show }) => {
  type ErrorState = Partial<Record<keyof Settings | 'general', string>>;
  
  const [settings, setSettings] = useState<Settings>(defaultSettings);
  const [errors, setErrors] = useState<ErrorState>({});
  const [isLoading, setIsLoading] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [pendingSettings, setPendingSettings] = useState<Partial<Settings>>({});

  useEffect(() => {
    if (show && !isLoading && Object.keys(settings).length === 0) { // Only fetch if not loading and settings are empty
        fetchSettings();
    }
}, [show, isLoading, settings]);

  const fetchSettings = async () => {
    try {
      setIsLoading(true);
      setErrors({});
      const response = await axiosInstance.get('/api/settings');
      const data = response.data;
      setSettings(data);
    } catch (error) {
      setErrors({ general: error instanceof Error ? error.message : 'An unknown error occurred' });
    } finally {
      setIsLoading(false);
    }
  };

  useDebouncedEffect(
    () => {
      if (Object.keys(pendingSettings).length > 0) {
        updateSettings(pendingSettings);
        setPendingSettings({});
      }
    },
    500,
    [pendingSettings]
  );

  const handleSettingChange = (key: keyof Settings, value: any) => {
    // Update UI immediately
    setSettings(prev => ({ ...prev, [key]: value }));
    
    // Queue the update
    setPendingSettings(prev => ({ ...prev, [key]: value }));

    // Apply immediate UI changes if needed
    if (key === 'theme') {
      document.documentElement.setAttribute('data-theme', value);
    }
  };

  const updateSettings = async (updates: Partial<Settings>) => {
    try {
      setIsSaving(true);
      const response = await axiosInstance.patch('/api/settings', updates);
      const data = response.data;
      setSettings(prev => ({ ...prev, ...data }));
    } catch (error) {
      setErrors(prev => ({
        ...prev,
        ...Object.keys(updates).reduce((acc, key) => ({
          ...acc,
          [key]: error instanceof Error ? error.message : 'An unknown error occurred'
        }), {})
      }));

      // Revert UI changes for failed updates
      setSettings(prev => ({
        ...prev,
        ...Object.keys(updates).reduce((acc, key) => ({
          ...acc,
          [key]: prev[key as keyof Settings]
        }), {})
      }));
    } finally {
      setIsSaving(false);
    }
  };

  if (!show) return null;

  return (
    <BasePanel title="Settings" show={show}>
      <div className="p-4">
        <FormControlLabel
          control={
            <Switch
              checked={settings.theme === 'dark'}
              onChange={(e) => handleSettingChange('theme', e.target.checked ? 'dark' : 'light')}
              disabled={isSaving}
            />
          }
          label="Dark Mode"
        />
        {errors.theme && (
          <div className="text-red-500 text-sm mt-1">{errors.theme}</div>
        )}
      </div>
    </BasePanel>
  );
};

export default Settings;
