export const API_URL = '/api';  // Always use /api prefix

// Environment configuration
export const IS_OFFLINE = process.env.REACT_APP_OFFLINE === 'true';  // Respect environment variable

// API configuration
export const API_CONFIG = {
  baseURL: IS_OFFLINE 
    ? process.env.REACT_APP_LOCAL_API_URL || 'http://localhost:3001'
    : process.env.REACT_APP_ONLINE_API_URL || '',
  timeout: 30000,
};

// Feature flags
export const FEATURES = {
  AI_ENABLED: !IS_OFFLINE, // Only enable AI features in online mode
  SYNC_ENABLED: !IS_OFFLINE, // Only enable sync in online mode
  LOCAL_STORAGE: IS_OFFLINE, // Use local storage in offline mode
};
