import { Note } from '../types';
import axiosInstance from './axiosConfig';

type CreateNoteResponse = {
  note?: Note;
  error?: string;
};

type ExpandSectionResponse = {
  content?: string;
  error?: string;
};

type ConnectionsResponse = {
  connections?: Array<{
    noteId: string;
    title: string;
    relevance: number;
  }>;
  error?: string;
};

type TocResponse = {
  toc?: Array<{
    level: number;
    title: string;
    id: string;
  }>;
  error?: string;
};

type ReorganizationResponse = {
  suggestions?: Array<{
    title: string;
    reason: string;
  }>;
  error?: string;
};

export class NoteAssistantService {
  static async createNoteFromTopic(topic: string): Promise<CreateNoteResponse> {
    try {
      const response = await axiosInstance.post('/api/note-assistant/create', { topic });
      return { note: response.data };
    } catch (error: any) {
      console.error('Error creating note:', error);
      return { error: error.response?.data?.error || 'Failed to create note' };
    }
  }

  static async expandSection(content: string, section: string): Promise<ExpandSectionResponse> {
    try {
      const response = await axiosInstance.post('/api/note-assistant/expand-section', { content, section });
      return { content: response.data.content };
    } catch (error: any) {
      console.error('Error expanding section:', error);
      return { error: error.response?.data?.error || 'Failed to expand section' };
    }
  }

  static async suggestConnections(noteId: string): Promise<ConnectionsResponse> {
    try {
      const response = await axiosInstance.post('/api/note-assistant/suggest-connections', { noteId });
      return { connections: response.data.connections };
    } catch (error: any) {
      console.error('Error suggesting connections:', error);
      return { error: error.response?.data?.error || 'Failed to suggest connections' };
    }
  }

  static async generateTableOfContents(content: string): Promise<TocResponse> {
    try {
      const response = await axiosInstance.post('/api/note-assistant/generate-toc', { content });
      return { toc: response.data.toc };
    } catch (error: any) {
      console.error('Error generating table of contents:', error);
      return { error: error.response?.data?.error || 'Failed to generate table of contents' };
    }
  }

  static async suggestReorganization(noteId: string): Promise<ReorganizationResponse> {
    try {
      const response = await axiosInstance.post('/api/note-assistant/suggest-reorganization', { noteId });
      return { suggestions: response.data.suggestions };
    } catch (error: any) {
      console.error('Error suggesting reorganization:', error);
      return { error: error.response?.data?.error || 'Failed to suggest reorganization' };
    }
  }

  static async createLinkedNote(sourceNoteId: string, title: string): Promise<CreateNoteResponse> {
    try {
      const response = await axiosInstance.post('/api/note-assistant/create-linked', { sourceNoteId, title });
      return { note: response.data.note };
    } catch (error: any) {
      console.error('Error creating linked note:', error);
      return { error: error.response?.data?.error || 'Failed to create linked note' };
    }
  }
}
