import axios, { AxiosError, AxiosInstance, AxiosRequestConfig } from 'axios';
import { authService } from './auth';
import { IS_OFFLINE, API_CONFIG } from '../config';

const API_URL = process.env.REACT_APP_API_URL || '/api';

console.log('API Config:', {
  API_URL,
  fullURL: IS_OFFLINE ? API_URL : `${process.env.REACT_APP_API_PREFIX}${API_URL}`
});

// Extend AxiosRequestConfig to include retry
interface ExtendedAxiosRequestConfig extends AxiosRequestConfig {
  retry?: number;
  __retryCount?: number;
}

// Create axios instance
const axiosInstance = axios.create({
  baseURL: API_CONFIG.baseURL,  // Use the configured baseURL
  withCredentials: true,
  timeout: API_CONFIG.timeout,
  headers: {
    'Content-Type': 'application/json'
  }
}) as AxiosInstance & { isAxiosError: typeof axios.isAxiosError };

console.log('Axios instance configured with baseURL:', API_CONFIG.baseURL);

// Add retry interceptor
axiosInstance.interceptors.request.use((config) => {
  // Add retry configuration to all requests
  (config as ExtendedAxiosRequestConfig).retry = 3;
  
  // Log the full URL being requested
  console.log('Making request to:', `${config.baseURL}${config.url}`);
  return config;
});

axiosInstance.interceptors.response.use(undefined, async (err) => {
  const config = err.config as ExtendedAxiosRequestConfig;
  
  // If config does not exist or retry option is not set, reject
  if (!config || !config.retry) return Promise.reject(err);
  
  // Set the variable for keeping track of the retry count
  config.__retryCount = config.__retryCount || 0;
  
  // Check if we've maxed out the total number of retries
  if (config.__retryCount >= config.retry) {
    // Reject with the error
    return Promise.reject(err);
  }
  
  // Increase the retry count
  config.__retryCount += 1;
  
  // Create new promise to handle exponential backoff
  const backoff = new Promise(resolve => {
    setTimeout(() => {
      resolve(null);
    }, 1000 * Math.pow(2, (config.__retryCount || 0))); // Exponential backoff
  });
  
  // Return the promise in which recalls axios to retry the request
  await backoff;
  return axiosInstance(config);
});

// Add a request interceptor
axiosInstance.interceptors.request.use(
  (config) => {
    // In offline mode, reject all API calls immediately
    if (IS_OFFLINE) {
      return Promise.reject(new Error('API calls are disabled in offline mode'));
    }

    const token = authService.getToken();
    console.log('Request interceptor - Token:', token ? 'Present' : 'Not present');
    console.log('IS_OFFLINE:', IS_OFFLINE);
    
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    
    console.log('Making request:', {
      url: config.url,
      baseURL: config.baseURL,
      method: config.method,
      headers: config.headers,
      data: config.data
    });
    return config;
  },
  (error) => {
    console.error('Request interceptor error:', error);
    return Promise.reject(error);
  }
);

// Add a response interceptor
axiosInstance.interceptors.response.use(
  (response) => response,
  (error: AxiosError) => {
    // Log the error for debugging
    console.error('API Error:', {
      status: error.response?.status,
      data: error.response?.data,
      message: error.message
    });

    if (error.response?.status === 401) {
      authService.logout();
      window.location.href = '/login';
    } else if (error.code === 'ECONNABORTED') {
      console.error('Request timed out');
    } else if (error.response) {
      console.error('Server error:', error.response.status, error.response.data);
    } else if (error.request) {
      console.error('No response received:', error.message);
    } else {
      console.error('Error setting up request:', error.message);
    }
    return Promise.reject(error);
  }
);

export default axiosInstance;
