import { Note } from '../types';
import axiosInstance from './axiosConfig';
import type { AxiosResponse } from 'axios';
import { useCallback } from 'react';

interface AIResponse {
  content: string;
  error?: string;
}

// Cache for AI responses
const aiCache = new Map<string, {
  data: any;
  timestamp: number;
  ttl: number;
}>();

const CACHE_TTL = 5 * 60 * 1000; // 5 minutes
const RATE_LIMIT_WINDOW = 60 * 1000; // 1 minute
const MAX_REQUESTS_PER_WINDOW = 10;

let requestCount = 0;
let windowStart = Date.now();

const checkRateLimit = () => {
  const now = Date.now();
  if (now - windowStart > RATE_LIMIT_WINDOW) {
    requestCount = 0;
    windowStart = now;
  }
  
  if (requestCount >= MAX_REQUESTS_PER_WINDOW) {
    throw new Error('Rate limit exceeded. Please try again later.');
  }
  
  requestCount++;
};

const getCachedResponse = (cacheKey: string) => {
  const cached = aiCache.get(cacheKey);
  if (cached && Date.now() - cached.timestamp < cached.ttl) {
    return cached.data;
  }
  return null;
};

const setCachedResponse = (cacheKey: string, data: any, ttl = CACHE_TTL) => {
  aiCache.set(cacheKey, {
    data,
    timestamp: Date.now(),
    ttl
  });
};

export class AIService {
  static async generateSuggestions(currentNote: Note, allNotes: Note[]): Promise<string[]> {
    try {
      const cacheKey = `suggestions:${currentNote.id}:${currentNote.last_modified}`;
      const cached = getCachedResponse(cacheKey);
      if (cached) return cached;

      checkRateLimit();

      const response: AxiosResponse<{ suggestions: string[] }> = await axiosInstance.post(`/api/ai/suggestions`, {
        noteContent: currentNote.content,
        contextNotes: allNotes.map(note => ({
          id: note.id,
          title: note.title,
          content: note.content,
          tags: note.tags,
        })),
      });

      setCachedResponse(cacheKey, response.data.suggestions);
      return response.data.suggestions;
    } catch (error) {
      console.error('Error generating suggestions:', error);
      return [];
    }
  }

  static async generateTags(content: string): Promise<string[]> {
    try {
      const cacheKey = `tags:${content}`;
      const cached = getCachedResponse(cacheKey);
      if (cached) return cached;

      checkRateLimit();

      const response: AxiosResponse<{ tags: string[] }> = await axiosInstance.post(`/api/ai/tags`, { content });
      setCachedResponse(cacheKey, response.data.tags);
      return response.data.tags;
    } catch (error) {
      console.error('Error generating tags:', error);
      return [];
    }
  }

  // Add AbortController for cancellable requests
  static async generateContent(
    prompt: string,
    onToken?: (token: string) => void,
    signal?: AbortSignal
  ): Promise<{ content?: string; error?: string }> {
    try {
      const cacheKey = `content:${prompt}`;
      const cached = getCachedResponse(cacheKey);
      if (cached) return { content: cached };

      checkRateLimit();

      const response: AxiosResponse<ReadableStream> = await axiosInstance.post(
        `/api/ai/generate`,
        { prompt },
        {
          responseType: 'stream',
          signal
        }
      );

      if (response.status !== 200) {
        return { error: 'Failed to generate content' };
      }

      let content = '';
      const decoder = new TextDecoder();

      const stream = response.data as any;
      stream.on('data', (chunk: Buffer) => {
        const lines = decoder.decode(chunk).split('\n').filter(Boolean);

        for (const line of lines) {
          if (!line.startsWith('data:')) continue;
          const data = line.slice(5);

          try {
            const parsed = JSON.parse(data);
            if (parsed.token) {
              content += parsed.token;
              onToken?.(parsed.token);
            }
          } catch (e) {
            console.error('Error parsing SSE data:', e);
          }
        }
      });

      await new Promise((resolve, reject) => {
        stream.on('end', resolve);
        stream.on('error', reject);
        if (signal) {
          signal.addEventListener('abort', () => {
            stream.destroy();
            reject(new Error('Request aborted'));
          });
        }
      });

      setCachedResponse(cacheKey, content);
      return { content };
    } catch (error: unknown) {
      if (error instanceof DOMException && error.name === 'AbortError') {
        return { error: 'Request cancelled' };
      }
      return { error: 'Failed to generate content' };
    }
  }

  static async summarize(content: string): Promise<string> {
    try {
      const cacheKey = `summary:${content}`;
      const cached = getCachedResponse(cacheKey);
      if (cached) return cached;

      checkRateLimit();

      const response: AxiosResponse<{ summary: string }> = await axiosInstance.post(`/api/ai/summarize`, { content });
      setCachedResponse(cacheKey, response.data.summary);
      return response.data.summary;
    } catch (error) {
      console.error('Error summarizing content:', error);
      return '';
    }
  }

  static async findRelatedNotes(sourceNote: Note, allNotes: Note[]): Promise<Array<{ noteId: string; relevance: number; context: string }>> {
    try {
      const cacheKey = `related:${sourceNote.id}:${sourceNote.last_modified}`;
      const cached = getCachedResponse(cacheKey);
      if (cached) return cached;

      checkRateLimit();

      const response: AxiosResponse<{ relatedNotes: Array<{ noteId: string; relevance: number; context: string }> }> = await axiosInstance.post(`/api/ai/related`, {
        sourceNote,
        allNotes
      });

      setCachedResponse(cacheKey, response.data.relatedNotes);
      return response.data.relatedNotes;
    } catch (error) {
      console.error('Error finding related notes:', error);
      return [];
    }
  }
}
